/* eslint-disable no-console  */

// Load our actual test file
import promiseTest from "promise";
import asyncTest from "async";

// Do it promise based
promiseTest().then(
  function(results) {
    console.log("Promise based:");
    console.log(results);
  },
  function(err) {
    console.log(err);
  }
);


asyncTest().then(
  function(results) {
    console.log("Async based:");
    console.log(results);
  },
  function(err) {
    console.log(err);
  }
);
